import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import links from 'data/links.yml';
import linkify from 'components/linkify';

export default class McKinsey extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.header}>
            <div className={styles.content}>
              <div className={styles.headerTitle}>{linkify(text.pages.header3[__LANG__])}</div>
              <div className={styles.cols}>
                <div className={styles.wrapInfo}>
                  <h2 className={styles.title}>{linkify(text.pages.alcatel.text1[__LANG__])}</h2>
                  <div className={styles.info}>
                    <p>
                      {linkify(text.pages.alcatel.text2[__LANG__])} <a
                      href={links.alcatel} target='_blank'>{linkify(text.pages.alcatel.company[__LANG__])}</a> {linkify(text.pages.alcatel.text3[__LANG__])}
                    </p>
                    <p className={styles.infoFooter}>{linkify(text.pages.alcatel.text4[__LANG__])}</p>
                  </div>
                </div>
                <div className={styles.logo1} />
              </div>
            </div>
          </div>
          <div className={styles.mainContent}>
            <div className={styles.main}><div className={styles.img2}/></div>
            {/*<div className={styles.img2}/>*/}
            <p className={styles.info}>{linkify(text.pages.alcatel.text5[__LANG__])}</p>
            <div className={styles.img3}/>
            <p className={styles.info}>{linkify(text.pages.alcatel.text6[__LANG__])}</p>
            <div className={styles.img4}/>
          </div>
          <div className={styles.border}></div>
          <Feedback/>
        </div>
      </InnerPage>
    );
  }

}



