import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import linkify from 'components/linkify';
import links from 'data/links.yml';

export default class XsollaPage2 extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>{linkify(text.pages.header[__LANG__])}</div>
            <h2 className={styles.text2}>{linkify(text.pages.xsolla3.text1[__LANG__])}</h2>
            <div className={styles.cols}>
              <div className={styles.text3}>
                <p>{linkify(text.pages.xsolla3.text2[__LANG__])}</p>
                <p>{linkify(text.pages.xsolla3.text3[__LANG__])} <a
                  href={links.xsolla}>{linkify(text.pages.xsolla3.text4[__LANG__])}</a> {linkify(text.pages.xsolla3.text5[__LANG__])} <a
                  href={links.xsollaBook}>{linkify(text.pages.xsolla3.text6[__LANG__])}</a>
                  {linkify(text.pages.xsolla3.text7[__LANG__])}
                </p>
                <p className={styles.text4}>{linkify(text.pages.xsolla3.text8[__LANG__])}</p>
              </div>
              <div className={styles.divX}>
                <div className={styles.img}></div>
              </div>
            </div>
            <p className={styles.text5}>{linkify(text.pages.xsolla3.text9[__LANG__])}</p>
            <div className={styles.img1}></div>
            <div className={styles.img2}></div>
            <div className={styles.img3}></div>
            <div className={styles.img4}></div>
            <div className={styles.img7}></div>
            <div className={styles.img8}></div>
            <div className={styles.img9}></div>
            <div className={styles.img10}></div>
            <p className={styles.text5}>{linkify(text.pages.xsolla3.text10[__LANG__])}</p>
            <div className={styles.img5}></div>
            <div className={styles.img6}></div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback/>
      </InnerPage>
    );
  }

}



